import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import Bmob from "hydrogen-js-sdk";
import mine from "@/utils/mine.js";
import common from "@/utils/common.js";

import uniPopup from '@/components/uni-popup/uni-popup.vue'

let that;
export default {
	components: {
		faIcon,
		uniPopup
	},
	data() {
		return {
			userInfo: '',
			modal_show: false,
			identity: uni.getStorageSync("identity"),
			sexs: ["男", "女"],
			vipShow:false,
			user:''
		}
	},
	onLoad() {
		that = this;
		that.user = uni.getStorageSync("user");
		const query = Bmob.Query('wc_setting');
		query.get('FtIO888D').then(res => {
		  //console.log(res)
			if(that.user.objectId == "aXcyRBaf"){
				that.vipShow = false
			}else{
				that.vipShow = res.isShow
			}
		}).catch(err => {
		  console.log(err)
		})
	},

	onShow() {
		that.userInfo = uni.getStorageSync("user")
		that.userInfo.vip_time = common.js_date_time(that.userInfo.vip_time)
	},

	methods: {
		setheaderimg() {
			//console.log("sss")
			if (that.identity == 1) {
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function(res) {
						console.log(res);
						let file;
						file = Bmob.File(that.userInfo.nickName + ".png", res.tempFilePaths[0]);
						file.save().then(res => {
							console.log("图片地址", res)
							that.userInfo.avatarUrl = res[0].url;
							mine.update_user(that.userInfo).then(res => {
								if (res) {
									uni.setStorageSync("user", that.userInfo)
								}
							})
						})

					},
				});

			} else {
				uni.showToast({
					title: "员工暂不支持修改",
					icon: "none"
				})
			}

		},

		//确定用户的姓名
		confrim_username() {
			if (that.identity == 1) {
				mine.update_user(that.userInfo).then(res => {
					if (res) {
						uni.showToast({
							title: "修改成功",
							icon: 'none'
						})
						that.modal_show = false
						uni.setStorageSync("user", that.userInfo)
						that.userInfo = uni.getStorageSync("user")
					}
				})
			} else {
				uni.showToast({
					title: "员工暂不支持修改",
					icon: "none"
				})
			}
		},

		//选择性别
		chooseSex(e) {
			let sex = Number(e.detail.value) + 1
			that.userInfo.sex = sex
			if (that.identity == 1) {
				mine.update_user(that.userInfo).then(res => {
					if (res) {
						uni.showToast({
							title: "修改成功",
							icon: 'none'
						})
						uni.setStorageSync("user", that.userInfo)
						that.userInfo = uni.getStorageSync("user")
					}
				})
			} else {
				uni.showToast({
					title: "员工暂不支持修改",
					icon: "none"
				})
			}
		},

	}
}
